* {
    margin: 0;
    padding: 0;
}

body {
    background-color: rgb(5, 5, 5);
}

.Header {
    width: 80vw;
    margin: auto;
    text-align: center;
    padding: 10px;
}

.items {
    width: fit-content;
    margin: auto;
    background-color: white;
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
}

.item {
    margin: 10px;
    font-weight: bold;
    width: 4.2rem;
    cursor: pointer;
}

.item::after {
    content: "";
    width: 0;
    height: 3px;
    background-color: black;
    display: block;
    margin: auto;
}

.item:hover::after {
    width: 100%;
    transition: 0.5s;
}

.main--container {
    width: 80vw;
    margin: auto;
}

.menu--item {
    background-color: white;
    width: 300px;
    margin: 10px auto;
    border: 2px solid aqua;
    border-radius: 5px;
}

.menu--img {
    border-radius: 5px;
    width: 100%;
}

.menu--info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2px 15px;
}

.desc {
    padding: 3px 15px;
}

@media only screen and (min-width:600px) {
    .main--container {
        display: grid;
        grid-template-columns: auto auto;
        gap: 10px;
    }
}

@media only screen and (min-width:800px) {
    .main--container {
        display: grid;
        grid-template-columns: auto auto;
    }
    .menu--item {
        display: flex;
        align-items: center;
        background-color: white;
        width: 500px;
        height: fit-content;
    }
    .menu--img {
        width: 200px;
        height: 180px;
    }
}